.tabs-history .ant-tabs-tab{
     color: #fff;
}

.tabs-history .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn{
    color: #ccfd07 !important;
}

.ant-tabs-top >.ant-tabs-nav::before{
    border-bottom: none;
}

.ant-tabs-tab{

    margin-left: 0 !important;
}
.ant-tabs-tab .ant-tabs-tab-btn button{

    padding: 10px;
    border: 1px solid #fff;
}
.ant-tabs-tab .all-button{
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
.ant-tabs-tab .canceled-button{
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.ant-tabs-ink-bar{
    display: none;
}